body, html{
  margin: 0;
  padding: 0;
  font-family: "Arial", sans-serif;
  background-color: #e1e98b;
}

.field{
  margin: 40px 0;
  padding: 0;
  position: relative;
}

.box{
  width: 30%;
  margin: 0 auto;
  margin-bottom: 20px;
  padding: 10px;
  border: 2px solid #a9b342;
  border-radius: 5px;
}

.text{
  font-size: 18px;
  font-weight: bold;
  color: #25270f;
  padding: 10px 5px;
}

.btn{
  border: none;
  margin: 5px;
  color: #fff;
  background-color: transparent;
  font-size: 14px;
  padding: 5px 10px;
  text-transform: uppercase;
  cursor: pointer;
}

.light{
  background-color: #59b8c8;
  border: 1px solid #07272c;
  border-radius: 3px;
}

.red{
  background-color: #d7a46f;
  border: 1px solid #543310;
  border-radius: 3px;
}

.new_task{
  background-color: #BC2863;
  border: 1px solid #9E494D;
  border-radius: 3px;
  position: absolute;
  top: 0;
  right: 20%;
}

#textedit{
  display: block;
  margin: 0 auto;
  width: 90%;
  color: rgba(0,0,0,.5);
  letter-spacing: 1.2px;
  font-style: italic;
  border: none;
  background-color: rgba(209, 214, 129, .6);
  border: 1px solid rgba(169, 179, 66, .5);
  padding: 10px;
}

.success{
  margin-top: 10px;
  background-color: #54ee4d;
  border: 1px solid #30672d;
  border-radius: 3px;
}



